*{
    box-sizing: border-box;
}

body{
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    line-height: 1.6;
    font-family: 'Josefin Sans', sans-serif;
    font-feature-settings: "palt";
    font-size: 1.4em;
    font-weight: 400;
    color: #111;
}

img{
    max-width: 100%;
    height: auto;
    line-height: 0;
}

:root{
    --font-size-sm: 14px;
    --font-size-normal: 16px;
    --font-size-lg: 24px;
    --font-size-x-lg: 32px;
    --font-size-xx-lg: 48px;
    --font-size-xxx-lg: 64px;

    --spacing-xx-s: 4px;
    --spacing-x-s: 8px;
    --spacing-s: 16px;
    --spacing-m: 32px;
    --spacing-l: 64px;
    --spacing-x-l: 128px;
    --spacing-xx-l: 256px;
    
    --color-black: #111;
    --color-white: #fff;
    --color-gray: #555;
    --color-lightgray: #999;
    --color-red: #f95b5b;
}

p{
    font-size: var(--font-size-normal);
}

h4{
    font-size: var(--font-size-lg);
}

h3{
    font-size: var(--font-size-x-lg);
}

h2{
    font-size: var(--font-size-xx-lg);
}

h1{
    font-size: var(--font-size-xxx-lg);
}

@media screen and (max-width: 768px){
    
    :root{
        --font-size-sm: 12px;
        --font-size-normal: 14px;
        --font-size-lg: 22px;
        --font-size-x-lg: 28px;
        --font-size-xx-lg: 44px;
        --font-size-xxx-lg: 56px;
        
        --spacing-xx-s: 3px;
        --spacing-x-s: 6px;
        --spacing-s: 12px;
        --spacing-m: 24px;
        --spacing-l: 48px;
        --spacing-x-l: 96px;
        --spacing-xx-l: 192px;
    }
    
}

@media screen and (max-width: 414px){
    
    :root{
        --font-size-sm: 10px;
        --font-size-normal: 12px;
        --font-size-lg: 16px;
        --font-size-x-lg: 24px;
        --font-size-xx-lg: 32px;
        --font-size-xxx-lg: 48px;
        
        --spacing-xx-s: 2px;
        --spacing-x-s: 4px;
        --spacing-s: 8px;
        --spacing-m: 16px;
        --spacing-l: 32px;
        --spacing-x-l: 64px;
        --spacing-xx-l: 128px;
    }
    
}